:root {
  /* sizes variables*/
  --safe-side-paddings: 20px;
  /* fonts variables */
  --basic-font: Heebo, Arial, sans-serif;
  --nav-link-size: 20px;
  --nav-link-weight: 500;
  --footer-copyright-size: 14px;
  --footer-copyright-weight: 400;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Heebo, Arial, sans-serif;
}

.container {
  margin: 0 auto;
  max-width: 1200px;
}

.content-container {
  margin: 0 auto;
  max-width: 1100px;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

@font-face {
  src: url("../fonts/Heebo-Bold.woff2") format("woff2"), url("../fonts/Heebo-Bold.woff") format("woff");
  font-family: Heebo;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-SemiBold.woff2") format("woff2"), url("../fonts/Heebo-SemiBold.woff") format("woff");
  font-family: Heebo;
  font-weight: 600;
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-Medium.woff2") format("woff2"), url("../fonts/Heebo-Medium.woff") format("woff");
  font-family: Heebo;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-Regular.woff2") format("woff2"), url("../fonts/Heebo-Regular.woff") format("woff");
  font-family: Heebo;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-Light.woff2") format("woff2"), url("../fonts/Heebo-Light.woff") format("woff");
  font-family: Heebo;
  font-weight: 300;
  font-display: swap;
}
:root {
  /* sizes variables*/
  --safe-side-paddings: 20px;
  /* fonts variables */
  --basic-font: Heebo, Arial, sans-serif;
  --nav-link-size: 20px;
  --nav-link-weight: 500;
  --footer-copyright-size: 14px;
  --footer-copyright-weight: 400;
}

/* global blocks */
.body {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  color: var(--primary-text);
  background-color: var(--bg);
}
.body--scrolloff {
  overflow: hidden;
}

.footer {
  width: 100%;
  padding: 50px var(--safe-side-paddings);
}

.footer__wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
}

.footer__social-list {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 35px;
}

.footer__social-item {
  height: 30px;
  width: 30px;
}

.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

.footer__copyright {
  font-family: var(--basic-font);
  font-size: var(--footer-copyright-size);
  font-weight: var(--footer-copyright-weight);
  color: var(--primary-text);
}

.footer__social-icon path {
  fill: var(--primary-text);
}
.footer__social-icon:hover path {
  fill: var(--hover-item-color);
}

.header {
  padding: 30px var(--safe-side-paddings);
  width: 100%;
}

.header__wrap {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 40px;
}

.main {
  width: 100%;
  padding: 80px var(--safe-side-paddings);
}

.main__wrap {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
}

.theme-switcher {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 0;
  border: none;
  padding: 5px;
  width: 106px; /* padding 5px + (32px icon size * 3 icons) */
}

/* Switcher Radio */
.theme-switcher__radio {
  appearance: none;
  appearance: none;
  margin: 0;
  width: 32px;
  height: 32px;
  background-position: center;
  background-size: 24px;
  background-repeat: no-repeat;
  transition: filter 0.1s ease-in;
  cursor: pointer;
}
.theme-switcher__radio:focus {
  outline: none;
}
.theme-switcher__radio--light {
  background-image: url("../images/icons/light.svg");
}
.theme-switcher__radio--auto {
  background-image: url("../images/icons/auto.svg");
}
.theme-switcher__radio--dark {
  background-image: url("../images/icons/dark.svg");
}

/* Switcher Status */
.theme-switcher__status {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  background-image: url("../images/icons/status.svg");
  background-position: center;
  background-size: 30px;
  background-repeat: no-repeat;
  transition: background-position 0.1s ease-in;
}

.theme-switcher__radio:focus-visible ~ .theme-switcher__status {
  box-shadow: 0 0 0 2px black;
}

.theme-switcher__radio--light:checked ~ .theme-switcher__status {
  background-position: left 6px center;
}

.theme-switcher__radio--auto:checked ~ .theme-switcher__status {
  background-position: center center;
}

.theme-switcher__radio--dark:checked ~ .theme-switcher__status {
  background-position: right 6px center;
}

/* blocks */
.nav-list {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  gap: 30px;
}

.nav-list__item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-list__link {
  font-family: var(--basic-font);
  font-weight: var(--nav-link-weight);
  font-size: var(--nav-link-size);
  text-decoration: none;
  text-transform: capitalize;
  color: var(--navigation-link-color);
  cursor: pointer;
}
.nav-list__link:hover {
  color: var(--hover-item-color);
}
.nav-list__link--active {
  color: var(--current-item-color);
  pointer-events: none;
}

.nav {
  display: flex;
}

.banner {
  display: grid;
  grid-template-columns: 1fr max-content;
  grid-template-rows: repeat(3, min-content);
  grid-template-areas: "title avatar" "info avatar" "button avatar";
  grid-column-gap: 100px;
}

.banner__title {
  font-family: var(--basic-font);
  grid-area: title;
  font-size: 44px;
  font-weight: 700;
  line-height: 60px;
  color: var(--primary-text);
}

.banner__info {
  font-family: var(--basic-font);
  grid-area: info;
  color: var(--primary-text);
  font-size: 16px;
  font-weight: 400;
}

.banner__download-resume {
  font-family: var(--basic-font);
  grid-area: button;
  color: var(--white-text);
  padding: 5px 17px;
  width: max-content;
  text-transform: capitalize;
  background-color: var(--important-item-color);
  border: none;
  border-radius: 2px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
}
.banner__download-resume:hover {
  background-color: var(--hover-item-color);
}

.banner__avatar {
  grid-area: avatar;
  width: 250px;
  height: 250px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: var(--shadow);
}